<template lang="pug">
.container.pos-r.container--small
  .row
    .col.col-md-12
      section.contact__content
        h2.text-center.contact__title Let&apos;s work together!
        p.text-center.contact__info
          | You can send an email to
          a.c-main(href="mailto:hou.dobaotrung@gmail.com") &nbsp;hou.dobaotrung@gmail.com
          | .
          br
          |
          | Or contact me through the form below.
        form.homeEmailForm(action="", method="POST", @submit.prevent="submitEmail")
          .row
            .col-12.col-md-6
              .formGroup.formGroup--stylized
                input.formGroup__input(
                  type="text",
                  required="",
                  v-multi-ref:stylizedInput,
                  v-model="formData.name"
                  @blur="stylize($event, 0, false)",
                  @focus="stylize($event, 0)"
                )
                label.formGroup__label(for="", v-multi-ref:stylizedLabel) What may I call you?
                span.dummy(v-multi-ref:stylizedDummy)
            .col-12.col-md-6
              .formGroup.formGroup--stylized
                input.formGroup__input(
                  type="email",
                  required="",
                  v-multi-ref:stylizedInput,
                  v-model="formData.email"
                  @blur="stylize($event, 1, false)",
                  @focus="stylize($event, 1)"
                )
                label.formGroup__label(for="", v-multi-ref:stylizedLabel) Your contact email
                span.dummy(v-multi-ref:stylizedDummy)
          .formGroup.formGroup--stylized
            input.formGroup__input(
              type="text",
              required="",
              v-multi-ref:stylizedInput,
              v-model="formData.subject"
              @blur="stylize($event, 2, false)",
              @focus="stylize($event, 2)"
            )
            label.formGroup__label(for="", v-multi-ref:stylizedLabel) Subject line
            span.dummy(v-multi-ref:stylizedDummy)
          .formGroup.formGroup--stylized
            textarea.formGroup__input(
              name="",
              cols="30",
              rows="10",
              required="",
              v-multi-ref:stylizedInput,
              v-model="formData.message",
              @blur="stylize($event, 3, false)",
              @focus="stylize($event, 3)"
            )
            label.formGroup__label(for="", v-multi-ref:stylizedLabel) Your message
            span.dummy(v-multi-ref:stylizedDummy)
          .text-center.formGroup
            button.formGroup__button Send me message
</template>

<script>
export default {
  name: "ContactMe",
  data() {
    return {
      stylizedInputNameList: [],
      formData: {
        name: "",
        email: "",
        subject: "",
        message: "",
      },
    };
  },
  methods: {
    submitEmail() {
      const body = `${this.formData.message}%0D%0A%0D%0AHere is my contact email: ${this.formData.email} - ${this.formData.name}`;

      window.open(`mailto:hou.dobaotrung@gmail.com?subject=${this.formData.subject}&body=${body}`);
    },
    stylize(e, index, isFocusIn = true) {
      const stylizedLabels = this.$refs.stylizedLabel;
      const stylizedDummies = this.$refs.stylizedDummy;

      const labelWidth = stylizedLabels[index].getBoundingClientRect().width;
      const inputWidth = e.target.getBoundingClientRect().width;

      if (isFocusIn) {
        stylizedDummies[index].setAttribute(
          "style",
          `clip: rect(0 ${inputWidth}px 1px ${labelWidth + 20}px)`
        );

        return;
      }

      if (e.target.value) {
        return;
      }

      stylizedDummies[index].setAttribute(
        "style",
        `clip: rect(0 ${inputWidth}px 1px 0)`
      );
    },
  },
  mounted() {
    const stylizedLabels = this.$refs.stylizedLabel;
    const stylizedDummies = this.$refs.stylizedDummy;
    const stylizedInputs = this.$refs.stylizedInput;

    if (stylizedLabels.length != stylizedDummies.length) {
      console.warn("Unmatched stylized number of input and label pair: ");
    }

    stylizedInputs.forEach((input, index) => {
      const fakeEvt = {
        target: input,
      };

      // Khởi tạo CSS cái clip để có thể animate
      this.stylize(fakeEvt, index, false);
    });
  },
};
</script>
