<template lang="pug">
  fragment
    div(ref="guide" style="opacity: 0")
      Guide(title="About me")

    .container.intro
      .intro__content.pos-r.h-100-vh
        div(ref="num" style="opacity: 0")
          span.intro__numberLeft 201
            span 5
          span.intro__numberRight
            span 2
            | 020

        .intro__left(ref="introLeft" style="opacity: 0")
          p I'm a Website Developer living in Hanoi, I started to work as a website developer since 2015. I'm thriving to work abroad, create things that matter, and help your business grows.

          .text-left.mt-3.mt-lg-4
            a(href="https://drive.google.com/file/d/1Pr99j3O6YejnQv1X6YQUfil_2Cbyqawn/view?usp=sharing" target="_blank").formGroup__button Download my CV

        .intro__right(ref="introRight" style="opacity: 0")
          span.intro__rightCapLetter(ref="letter1") H
          span.intro__rightCapLetter(ref="letter2") I
          <br>
          span.intro__name(ref="im") I'm
            .d-none.d-lg-block
            |&nbsp;Trung
            .d-none.d-lg-block
            |&nbsp;Do
          span.intro__profession(ref="title") Full-stack Website Developer

</template>

<script>
import Guide from '@/components/common/Guide'
import gsap from 'gsap'

export default {
  name: 'Intro',
  computed: {
    isInitted() {
      return this.$store.state.isInitted
    }
  },
  components: {
    Guide
  },
  watch: {
    isInitted(newVal) {
      if (newVal === true) {
        this.init();
      }
    }
  },
  methods: {
    init() {
      const tl = gsap.timeline({
        delay: 1.9
      });

      const fromParams = {
        opacity: 0
      };

      const toParams = {
        opacity: 1
      };


      tl.addLabel('final');
      tl.fromTo( this.$refs.letter1, 0.4, fromParams, toParams, 'final');
      tl.fromTo( this.$refs.introRight, 0.4, fromParams, toParams, 'final');

      tl.fromTo( this.$refs.introLeft, 0.4, fromParams, toParams, 'final' );
      tl.fromTo( this.$refs.guide, 0.4, fromParams, toParams, 'final' );
      tl.fromTo( this.$refs.num, 0.4, fromParams, toParams, 'final' );
    }
  },
}
</script>
