<template>
  <div class="preloader" ref="preloader">
    <div class="preloader__left" ref="left"></div>
    <div class="preloader__right" ref="right"></div>

    <div class="preloader__loadingGroup" ref="loadingGroup">
      <div class="preloader__loadingBar">
        <div class="progressBar" :style="{
          transform: `scale(${progress})`
        }">
        </div>
      </div>
      <div class="preloader__loadingText">
        Loading ...
        <span>{{ progressText }} %</span>
      </div>
    </div>
  </div>
</template>

<script>
import gsap from 'gsap'

export default {
  name: 'Preloader',
  computed: {
    progressText() {
      return parseFloat(this.progress * 100).toFixed(0);
    }
  },
  data() {
    return {
      progress: 0.0,
      progressStyle: {
        transform: `scale(${this.progress})`
      }
    }
  },
  mounted() {
    const tl = gsap.timeline({
      onComplete: () => {
        gsap.set(this.$refs.loadingGroup, {
          display: 'none'
        });

        removeTl.resume();
      }
    });

    tl.fromTo(this, 5, {
      progress: 0.0
    }, {
      progress: 0.3
    });

    tl.fromTo(this, 5, {
      progress: 0.3
    }, {
      progress: 0.7
    });

    tl.fromTo(this, 3, {
      progress: 0.7
    }, {
      progress: 1.0
    });

    window.addEventListener('load', () => {
      tl.timeScale(8);
    });

    const removeTl = gsap.timeline({
      paused: true,
      onComplete: () => {
        this.$refs.preloader.remove();
        this.$store.commit('setInitState', true);
      }
    });

    const { left, right } = this.$refs;

    removeTl.addLabel('start');
    removeTl.fromTo(left, 1, {
      scaleX: 1,
      transformOrigin: 'left center'
    }, {
      scaleX: 0
    }, 'start');

    removeTl.fromTo(right, 1, {
      scaleX: 1,
      transformOrigin: 'right center'
    }, {
      scaleX: 0
    }, 'start');
  }
}
</script>
