<script>
/* eslint-disable no-unused-vars */

import gsap, { Power3 } from 'gsap';
import { lerp } from '@/utils';
import {
  TextureLoader,
  LinearFilter,
  ShaderMaterial,
  Mesh,
  Scene,
  PerspectiveCamera,
  WebGLRenderer,
  Vector2,
  DoubleSide,
  PlaneBufferGeometry,
  AdditiveBlending,
  ClampToEdgeWrapping,
  RepeatWrapping,
  Vector3
} from "three";

import vertexShader from '@/misc/intro/vs';
import fragmentShader from '@/misc/intro/fs';

import myWorkVS from '@/misc/mywork/vs';
import myWorkFS from '@/misc/mywork/fs';

import smokeVS from '@/misc/smoke/vs';
import smokeFS from '@/misc/smoke/fs';

import original from '@/assets/images/wolf-face2.png';
import target from '@/assets/images/myface-2.png';

/**
 * StartAnim và EndAnim nên chênh so với start & end tối thiểu 0.2s.
 * Lí do là vì ta cần animate sớm hơn trước khi ngừng render (visible = false)
 */
const animData = {
  intro: {
    start: 0.0,
    end: 0.9,
    startAnim: 0,
    endAnim: 0.8
  },
  mywork: {
    start: 0.6,
    end: 1.8,
    startAnim: 0.8,
    endAnim: 1.4
  }
};

const globalConfig = function() {
  this.blend = 0.0;
  this.scrollProgress = 0.0;
  this.mouse = {
    x: -1,
    y: 0
  };

  this.workBlend = 0.0;
  this.workBlendTl = gsap.timeline({
    paused: true
  });

  this.workBlendTl.to(this, 1.5, {
    workBlend: 1.0,
    ease: Power3.easeOut
  });

  this.projectList = [];
};

const data = new globalConfig();

const onScrollCanvas = function() {
  const { currentSection, sectionProgress } = this.$store.state.scrollState;

  data.currentSection = currentSection;
  data.sectionProgress = sectionProgress;
  data.scrollProgress = currentSection + sectionProgress;

  if (data.scrollProgress >= animData.mywork.startAnim && data.scrollProgress <= animData.mywork.endAnim) {
    data.workBlendTl.reversed( 0 )
      .timeScale(1)
      .play()
    ;
  } else {
    data.workBlendTl.timeScale(5).reversed(1);
  }
};

class MyCanvas {
  constructor(idOrDOM) {
    this.init(idOrDOM);
  }
  loadImages() {
    let image1Url = original;
    let image2Url = target;
    let loader = new TextureLoader();

    const promises = [];

    function processTexture(texture) {
      texture.minFilter = LinearFilter;
      texture.wrapS = ClampToEdgeWrapping;
      texture.wrapT = RepeatWrapping;
    }

    this.workImages = [];

    data.projectList.forEach((project, index) => {
      if (!project.image) console.warn('Invalid image for project ' + project.id);

      promises.push(
        new Promise(resolve => {
          loader.load(project.image, texture => {
            processTexture(texture);
            this.workImages[index] = texture;
            resolve();
          });
        })
      );
    });

    promises.push(
      new Promise(resolve => {
        loader.load(image1Url, texture => {
          processTexture(texture);
          this.image1 = texture;
          resolve();
        });
      })
    );

    promises.push(
      new Promise(resolve => {
        loader.load(image2Url, texture => {
          this.image2 = texture;
          resolve();
        });
      })
    );

    return Promise.all(promises);
  }
  async init(idOrDOM) {
    if (typeof idOrDOM == "string") {
      this.container = document.getElementById(idOrDOM);
    } else {
      this.container = idOrDOM;
    }

    this.state = {
      isActive: true,
      transitBlend: 0.0
    };

    this.time = 0;

    await this.loadImages();

    this.initScene();
    this.addObjectsToScene();
    this.onWindowResize();
    this.animate();
  }
  addObjectsToScene() {
    this.addIntroToScene();
    this.addMyWorkImage();
    this.addSmokeToScene();
  }
  addIntroToScene() {
    this.geometry = new PlaneBufferGeometry(0.6, 0.6, 1, 1);

    this.material = new ShaderMaterial({
      extensions: {
        derivatives: "#extension GL_OES_standard_derivatives : enable"
      },
      uniforms: this.uniforms,
      vertexShader,
      fragmentShader,
      depthTest: 1,
      transparent: 1
    });

    this.plane = new Mesh(this.geometry, this.material);
    this.plane.name = 'Intro';

    this.scene.add(this.plane);
  }
  addSmokeToScene() {
    const { clientWidth, clientHeight } = this.container;

    const smokeGeometry = new PlaneBufferGeometry(clientWidth / clientHeight, 1, 1, 1);

    const material = new ShaderMaterial({
      extensions: {
        derivatives: "#extension GL_OES_standard_derivatives : enable"
      },
      uniforms: this.uniforms,
      vertexShader: smokeVS,
      fragmentShader: smokeFS,
      transparent: 1
    });

    const plane = new Mesh(smokeGeometry, material);
    plane.name = 'Smoke';

    this.scene2.add(plane);
  }
  addMyWorkImage() {
    const geometry = new PlaneBufferGeometry(0.4 * 0.85, 0.3 * 0.85, 32, 32);
    const material = new ShaderMaterial({
      vertexShader: myWorkVS,
      fragmentShader: myWorkFS,
      uniforms: this.uniforms,
      side: DoubleSide,
      blending: AdditiveBlending
    });

    const workPlane = new Mesh(geometry, material);

    workPlane.position.y = 0.15;
    workPlane.position.z = 0.1;
    workPlane.rotation.x = -0.13;

    workPlane.name = 'MyWork';
    this.scene.add(workPlane);
  }
  onWindowResize() {
    const { clientWidth, clientHeight } = this.container;

    this.renderer.setSize(clientWidth, clientHeight, true);

    this.camera.aspect = clientWidth / clientHeight;
    this.camera.updateProjectionMatrix();

    if (window.innerWidth < 768) {
      this.plane.position.y = 0.1;
      this.plane.position.z = -0.3;

      const w = window.innerWidth;
      const h = window.innerHeight;

      const ratio = Math.max(w, h) / Math.min(w, h) + 0.5; // Thêm 0.5 cho đẹp

      this.scene2.getObjectByName('Smoke').scale.set(ratio, 1, 1);
      this.renderer.setPixelRatio(0.9);
    } else {

      this.plane.position.y = 0;
      this.plane.position.z = 0;

      this.scene2.getObjectByName('Smoke').scale.set(1, 1, 1);
      this.renderer.setPixelRatio(window.devicePixelRatio);
    }
  }
  initScene() {
    const { clientWidth, clientHeight } = this.container;

    // Global Uniforms
    this.uniforms = {
      blend: { value: data.blend },
      u_scrollProgress: {
        value: data.scrollProgress
      },
      u_resolution: {
        type: "vec2",
        value: new Vector2(clientWidth, clientHeight)
      },
      time: {
        value: this.time
      },
      original: {
        type: "t",
        value: this.image1
      },
      target: {
        type: "t",
        value: this.image2
      },
      u_workImage: {
        type: "t",
        value: this.currentWorkImage
      },
      u_nextWorkImage: {
        type: 't',
        value: this.nextWorkImage
      },
      u_mouse: {
        type: "vec2",
        value: new Vector2(data.mouse.x, data.mouse.y)
      },
      u_workBlend: {
        value: data.workBlend,
      },
      u_transitBlend: {
        value: this.state.transitBlend
      }
    };

    this.currentWorkImage = this.workImages[0];
    this.nextWorkImage = this.workImages[1];

    this.scene = new Scene();

    this.scene2 = new Scene();

    this.camera = new PerspectiveCamera(
      53,
      clientWidth / clientHeight,
      0.1,
      1000
    );
    this.camera.position.set(0, 0, 1);

    this.renderer = new WebGLRenderer({
      precision: 'highp',
      antialias: true,
      powerPreference: 'high-performance'
    });

    this.renderer.autoClear = false;
    this.renderer.setClearColor(0x000000, 0);
    this.renderer.getContext().getExtension("OES_standard_derivatives");

    this.container.appendChild(this.renderer.domElement);
  }
  update() {
    if (!this.uniforms) {
      return;
    }

    this.time += 0.01;
    this.uniforms.time.value = this.time;
    this.uniforms.blend.value = data.blend;
    this.uniforms.u_scrollProgress.value = data.scrollProgress;
    this.uniforms.u_mouse.value.x = data.mouse.x;
    this.uniforms.u_mouse.value.y = data.mouse.y;
    this.uniforms.u_workBlend.value = data.workBlend;
    this.uniforms.u_transitBlend.value = this.state.transitBlend;

    this.uniforms.u_workImage.value = this.currentWorkImage;
    this.uniforms.u_nextWorkImage.value = this.nextWorkImage;

    this.updateIntro();
    this.updateMyWork();
  }
  updateIntro() {
    const intro = this.scene.getObjectByName('Intro');

    if (!intro) return;

    if (data.scrollProgress >= animData.intro.start && data.scrollProgress <= animData.intro.end) {
      intro.visible = true;
    } else {
      intro.visible = false;
    }
  }
  updateMyWork() {
    const myWork = this.scene.getObjectByName('MyWork');

    if (!myWork) return;

    if (!data.sectionProgress) return myWork.visible = false;

    const { start, end } = animData.mywork;

    // Performance! Stop rendering when it isn't visible
    if (data.scrollProgress >= start && data.scrollProgress <= end) {
      myWork.visible = true;
    }
    else {
      myWork.visible = false;
    }
  }
  animate() {
    window.requestAnimationFrame(this.animate.bind(this));

    if (!this.state.isActive) {
      return;
    }

    this.update();
    this.render();
  }
  render() {
    this.renderer.clear();

    if (window.innerWidth > 768) {
      this.renderer.render(this.scene2, this.camera);
    }

    this.renderer.clearDepth();
    this.renderer.render(this.scene, this.camera);

  }
  unpause() {
    this.state.isActive = true;
  }
  pause(){
    this.state.isActive = false;
  }
  transit(targetIdx) {
    this.nextWorkImage = this.workImages[targetIdx];
    this.nextWorkImage.needsUpdate = true;

    gsap.fromTo(this.state, 1.0, {
      transitBlend: 0,
    }, {
      transitBlend: 1.0,
      onComplete: () => {
        this.currentWorkImage = this.workImages[targetIdx];
        this.currentWorkImage.needsUpdate = true;
        this.state.transitBlend = 0;
      }
    })
  }
}

export default {
  name: "IntroCanvas",
  methods: {
    onMousemove(e) {
      const { x } = data.mouse;

      data.mouse = {
        x: lerp(e.clientX / window.innerWidth * 2 - 1, x , 0.95), // Lerpppp
        y: -(e.clientY / window.innerHeight) * 2 + 1
      }
    },
    transit(toIndex) {
      this.canvas.transit(toIndex);
    },
    init() {
      data.projectList = this.projectList;

      this.canvas = new MyCanvas(this.$refs.canvas);

      document.addEventListener("mousemove", this.onMousemove);

      window.addEventListener("resize", this.canvas.onWindowResize.bind(this.canvas));
      window.addEventListener("scroll", this.onScrollCanvasFunc);

      this.onScrollCanvasFunc();
    },
    startAnimation() {
      gsap.fromTo(
        data,
        2.5,
        {
          blend: 0
        },
        {
          blend: 1.0
        }
      );
    }
  },
  data() {
    return {
      canvas: null,
      tl: null
    };
  },
  watch: {
    currentProject(newVal) {
      this.transit(newVal);
    },
    isInitted(newVal) {
      if (!newVal) {
        return;
      }

      this.startAnimation();
    },
    projectList() {
      this.init();
    }

  },
  computed: {
    isInitted() {
      return this.$store.state.isInitted
    },
    onScrollCanvasFunc() {
      return onScrollCanvas.bind(this);
    },
    currentProject() {
      return this.$store.state.projectState.currentProject;
    },
    projectList() {
      return this.$store.state.projectState.projectList;
    }
  },
  beforeDestroy() {
    cancelAnimationFrame(this.canvas.animate);
    document.removeEventListener("mousemove", this.onMousemove);
    document.removeEventListener("resize", this.canvas.onWindowResize);
    window.removeEventListener("scroll", this.onScrollCanvasFunc);
    this.canvas = null;
  }
};
</script>

<template lang="pug">
  .intro__canvas(ref="canvas")
</template>
