<template>
  <div
    v-touch:swipe.left="navigateNext"
    v-touch:swipe.right="navigatePrev"
  >
    <div class="project__image">
      <div class="project__imageWrapper"></div>
      <div class="project__nav text-center d-lg-none">
        <p :class="{
            'project__linkText text-center mb-0': true,
            'blinking': currentProject === 0,
            'd-none': currentProject !== 0
          }">
          Swipe for more work
          <svg xmlns="http://www.w3.org/2000/svg" width="30" fill="#606060" height="30" viewBox="0 0 490.7 490.7" enable-background="new 0 0 490.651 490.651">
            <path d="M437 43H309a11 11 0 000 21h128a11 11 0 000-21z"/>
            <path d="M445 46L402 3a11 11 0 00-15 15l35 35-35 35a11 11 0 0015 16l43-43c4-4 4-11 0-15z"/>
            <path d="M160 43H32a11 11 0 000 21h128a11 11 0 000-21z"/>
            <path d="M47 53l35-35A11 11 0 0067 3L24 46c-4 4-4 11 0 15l43 43a11 11 0 0015 0c4-5 4-11 0-16L47 53z"/>
            <path d="M427 213c-9 0-17 3-24 8a43 43 0 00-64-22 43 43 0 00-62-23v-69a43 43 0 00-85 0v181l-37-28a56 56 0 00-73 5 32 32 0 000 46l152 152c18 18 42 28 68 28h50c65 0 117-53 117-118V256c0-24-19-43-42-43zm21 160c0 53-43 96-96 96h-50c-20 0-39-7-53-22L97 296c-4-5-4-11 0-16a34 34 0 0145-3l54 41a11 11 0 0017-9V107a21 21 0 0143 0v160a11 11 0 0021 0v-54a21 21 0 0143 0v54a11 11 0 0021 0v-32a21 21 0 0143 0v32a11 11 0 0021 0v-11a21 21 0 0143 0v117z"/>
          </svg>
        </p>

        <a
          href="#"
          :key="project.id"
          v-for="(project, index) in projects"
          class="project__navBullet"
          :class="{
          'project__navBullet--active': index == currentProject
        }"
          @click="navigate(index, $event)"
        ></a>

      </div>
    </div>

    <article
      :key="project.id"
      v-multi-ref:projects
      v-for="(project, index) in projects"
      :class="{
        'project__item': true,
        'project__item--active': index == currentProject
      }"
      @mousedown="mousedown"
      @mouseup="mouseup"
    >
      <div class="pos-r">
        <div class="container container--small">
          <div class="project__row">
            <div class="row align-items-center">
              <div class="order-2 order-lg-1 col-12 col-lg-auto project__left">
                <p class="project__description">{{ project.description }}</p>
              </div>
              <div class="col order-1 order-lg-2 project__right">
                <h2 class="project__clientName">{{ project.clientName }}</h2>
                <h3 class="project__title">{{ project.title }}</h3>
              </div>
            </div>
          </div>
        </div>

        <div class="container text-right pos-r">
          <a class="project__link" target="_blank" :href="project.link">
            Visit site
            <span></span>
            <span></span>
          </a>
        </div>
      </div>
    </article>

    <div class="project__nav text-center d-none d-lg-block">
      <p :class="{
          'project__linkText text-center mb-2': true,
          'blinking': currentProject === 0,
          'invisible': currentProject !== 0
        }">
        Click or swipe for more work
      </p>

      <a
        href="#"
        :key="project.id"
        v-for="(project, index) in projects"
        class="project__navBullet"
        :class="{
        'project__navBullet--active': index == currentProject
      }"
        @click="navigate(index, $event)"
      ></a>
    </div>
  </div>
</template>

<script>
import gsap from "gsap";

export default {
  name: "ProjectList",
  data() {
    return {
      isAnimating: false,
      projects: [
        {
          id: 1,
          clientName: "Avalancha",
          title: "Developing new theme, building back-end solutions",
          image: "/images/clients/medstyle.png",
          description: `
            Medstyle is a Chilean healthcare corporation. My job was to collaborate with Avalancha team to improve the B2C site's Front-end overall quality
            , build B2B site based on a design, create back-end modules which help connecting to ERP's API.
            Related technologies: WordPress, REST API, HTML5, CSS3, Javascript.
          `,
          link: "http://avalanchadevs.com/medstyle/b2c",
        },
        {
          id: 2,
          clientName: "Theater theme",
          title: "Demonstrating my Front-end skills",
          image: "/images/clients/theater.jpg",
          description: `I make complex web applications. But also love making magnificient websites too.
          That's why I made this site just to demonstrate my Front-End skills. Related technologies: Canvas, WebGL, HTML, CSS3, Javascript`,
          link: "https://terrydo.github.io/theater",
        },
        {
          id: 3,
          clientName: "M'Mon Group",
          title: "Developing website",
          image: "/images/clients/mmon.jpg",
          description: `M'Mon is a Vietnamese start-up providing E-commerce solutions. I built it from scratch based on the given design.
            Related technologies: WordPress, HTML5, CSS3, Javascript`,
          link: 'https://mmongroup.vn',
        },
        {
          id: 4,
          clientName: "Lam Kinh Real",
          title: "Developing website",
          image: "/images/clients/bepos.jpg",
          description: `Lam Kinh Real is a real estate company located in Da Nang City. Technologies used: Nuxt.js, Vue.js`,
          link: "http://spa.bepos.io",
        },
        {
          id: 5,
          clientName: "iSpeak CRM",
          title: "Developing web application",
          image: "/images/clients/ispeak.jpg",
          description: `iSpeak CRM is one of my proudest project I've ever done.
            It's a complex system which helps all back office's activities a lot easier and more effective.
            Due to privacy concern, I'll only show a preview image and won't provide the link to the CRM as well as technologies used.
          `,
          link: "",
        },
      ],
      currentProject: 0,
    };
  },
  methods: {
    getNextProjectIndex() {
      return this.currentProject < this.projects.length - 1
        ? this.currentProject + 1
        : 0;
    },
    getPrevProjectIndex() {
      return this.currentProject == 0
        ? this.projects.length - 1
        : this.currentProject - 1;
    },
    navigate(index, e) {
      if (e) e.preventDefault();

      if (this.currentProject == index || this.isAnimating) return;

      this.isAnimating = true;

      const projects = this.$refs.projects;
      const currentProject = projects[this.currentProject];
      const nextProject = projects[index];

      const tl = gsap.timeline({
        onComplete: () => {
          this.isAnimating = false;
        },
      });

      tl.fromTo(
        currentProject,
        0.75,
        {
          opacity: 1,
        },
        {
          opacity: 0,
          onComplete: () => {
            this.currentProject = index;
          },
        }
      );

      tl.fromTo(
        nextProject,
        0.75,
        {
          opacity: 0,
        },
        {
          opacity: 1,
        }
      );

      this.$store.commit("setCurrentProject", index);
    },
    navigatePrev() {
      this.navigate(this.getPrevProjectIndex());
    },
    navigateNext() {
      this.navigate(this.getNextProjectIndex());
    },
    mousedown(e) {
      this.lastMouseClickedX = e.screenX;
    },
    mouseup(e) {
      const upX = e.screenX;
      // Swipe left
      if (upX - this.lastMouseClickedX <= -80) {
        this.navigateNext();
      } else if (upX - this.lastMouseClickedX >= 80) {
        this.navigatePrev();
      }
    },
    handleTouchEvent() {
      var xDown = null;
      var yDown = null;

      this.handleTouchStart = (evt) => {
        xDown = evt.touches[0].clientX;
        yDown = evt.touches[0].clientY;
      };

      this.handleTouchMove = (evt) => {
        if (!xDown || !yDown) {
          return;
        }

        let xUp = evt.touches[0].clientX;
        let yUp = evt.touches[0].clientY;

        let xDiff = xDown - xUp;
        let yDiff = yDown - yUp;

        if (Math.abs(xDiff) > Math.abs(yDiff)) {
          /*most significant*/
          if (xDiff > 0) {
            this.nextSection();
          } else {
            this.previousSection();
          }
        }

        /* reset values */
        xDown = null;
        yDown = null;
      };

      this.$refs.projects.forEach(project => {
        project.addEventListener("touchstart", this.handleTouchStart);
        project.addEventListener("touchmove", this.handleTouchMove);
      });
    },
  },
  mounted() {
    this.$store.commit("setProjectList", this.projects);
  },
  beforeDestroy() {
    this.$refs.projects.forEach(project => {
      project.removeEventListener("touchstart", this.handleTouchStart);
      project.removeEventListener("touchmove", this.handleTouchMove);
    });
  }
};
</script>
