<template lang="pug">
  fragment
    Preloader
    IntroCanvas
    Header

    .section(v-multi-ref:sections)
      Intro

    .section(v-multi-ref:sections).project
      MyWork
      Guide(title="My Work", number="02")

    .section.section--flow.section--centered.contact(v-multi-ref:sections)
      ContactMe
      Guide(title="Contact Me", number="03")
</template>

<script>

  import Preloader from '@/components/common/Preloader';
  import Guide from '@/components/common/Guide'
  import Header from '@/components/common/Header'
  import Intro from '@/components/home/Intro'
  import MyWork from '@/components/home/MyWork'
  import ContactMe from '@/components/home/ContactMe'
  import IntroCanvas from '@/components/home/IntroCanvas'

  const updateScrollState = function() {
    const sections = this.$refs.sections;

    let currentSection = 0;
    let sectionProgress = 0.0;
    const sectionCount = sections.length;

    for (let index = sectionCount - 1; index >= 0; index--) {
      const section = sections[index];
      const elRect = section.getBoundingClientRect();
      const elTop = elRect.top;

      if (elTop < 0) {
        currentSection = index;
        sectionProgress = Math.abs(elTop) / elRect.height;
        break;
      }
    }

    this.$store.commit('setScrollState', {
      currentSection,
      sectionProgress
    });
  }

  const updateClickState = function() {
    this.$store.commit('setClickState', {
      clicked: true
    });
  }

  export default {
    computed: {
      updateScrollStateFunc() {
        return updateScrollState.bind(this);
      },
      updateClickStateFunc() {
        return updateClickState.bind(this);
      }
    },
    components: {
      IntroCanvas,
      Header,
      Intro,
      Guide,
      MyWork,
      ContactMe,
      Preloader
    },
    mounted() {
      window.addEventListener('mousedown', this.updateClickStateFunc);
      window.addEventListener('scroll', this.updateScrollStateFunc);
    },
    beforeDestroy () {
      window.removeEventListener('scroll', this.updateScrollStateFunc);
      window.removeEventListener('mousedown', this.updateClickStateFunc);
    }
  }
</script>
